@import url('https://fonts.googleapis.com/css2?family=Alata&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Pro&display=swap');

.header{
    display: flex;
    align-items: center;
    color: white;
    background-color: black;
    top: 0;
    position: sticky;
    z-index: 2000;
}

.header__left{
    flex: 0.4;
}

.header__left > h1{
    text-align: center;
    font-family: 'Alata', sans-serif;
    letter-spacing: 1px;
    padding: 20px 0;
}

.header__left > h1 > span{
    color: #f64c08;
}

.header__right{
    display: flex;
    flex: 0.6;
    justify-content: space-evenly;
    align-items: center;
}

.header__right > a > h4{
    font-size: 18px;
    cursor: pointer;
    transition: transform 200ms ease-in-out;
    font-family: 'crimpson pro',sans-serif;
    padding: 20px 0;
    letter-spacing: 1px;
}

.header__right > a > h4:hover{
    transform: scale(1.09);
    background-color: #f64c08;
    color: white;
    padding: 5px 20px;
    border-radius: 2px;
    letter-spacing: 1px;
    font-family: 'crimpson pro',sans-serif;
    font-size: 18px;
    cursor: pointer;
}

.header__rightButton{
    background-color: #f64c08;
    color: white;
    padding: 5px 20px;
    border-radius: 2px;
    cursor: pointer;
}

.header__mobicons{
    display: none;
    font-size: 18px;
    cursor: pointer;
    padding: 20px 0;
    letter-spacing: 1px;
}

@media screen and (max-width: 800px) {
    .header{
        justify-content: space-evenly;
    }
    .header__mobicons{
        display: flex;
    }

    .header__right{
        display: none;
        
    }

    .header__right.active{
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        top: 83px;
        background-color: black;
        padding-bottom: 20px;
    }

    .header__right > a > h4{
        padding: 10px 0;
        border-bottom: 1px solid #f64c08;
    }

    
}