@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

.projectContainer{
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    margin: 200px 0;
    align-items: center;
}

.projectContainer > h1{
    padding-bottom: 15px;
}

.projectContainer > p{
    padding-bottom: 20px;
}

.projectContainer__projects{
    display: flex;
    width: 60vw;
    margin-top: 50px;
    flex-wrap: wrap;
}

@media screen and (max-width: 800px) {

    .projectContainer{
        margin: 130px 0;
    }
    
    .projectContainer__projects{
        justify-content: center;
        margin-top: 10px;
    }
}