.project{
    /* color: white; */
    margin: 15px;
    width: 350px;
    height: 170px; 
    transition: transform 100ms ease-in;
}
.project > img{
    width: 350px;
    height: 170px;
}

.project:hover{
    color: white;
    background-color: #f64c08;
    transform: scale(1.05);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.project__content{
    padding: 10px;
}
.project__content > h4{
    padding-bottom: 5px;
}

.project__content > p{
    font-size: 15px;
}

@media screen and (max-width: 800px) {
    .project > img{
        width: 314px;
        margin: 0 20px;
    }
}