.contact{
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    margin: 200px 0 100px;
    align-items: center;
}

.contact__container{
    border: 1px solid white;
    width: 600px;
    height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
    margin-top: 20px;
}

.contact__container > p{
    font-size: 20px;
    text-align: left;
    padding-bottom: 30px;
}

.contact__container > p > span{
    color: #f64c08;
}



.css-ci5apu-MuiSvgIcon-root{
    color: white !important;
}

@media screen and (max-width: 800px){

    .contact{
        margin: 10px 0 100px;
    }
    .contact__container{
        width: 240px;
    }

    .contact__container > p{
        font-size: 17px;
    }
}