@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

.topcontent{
    color: white;
    position: relative;
}

.topcontent__container{
    position: absolute;
    top: 300px;
    /* right: 150px; */
    left: 150px;
}

.topcontent__container > p{
    letter-spacing: 1px;
    text-align: left;
    padding-left: 3px;
    padding-top: 20px;
}

.topcontent__container > a > button{
    margin-top: 30px;
    text-align: left;
    margin-left: 5px;
    border: none;
    border-radius: 2px;
    outline: none;
}

.topcontent__container > h1{
    font-size: 30px;
    font-family: 'Montserrat', sans-serif;
}

.topcontent__downloadbutton{
    color: white;
    background-color: #f64c08;
    font-size: 14px;
    padding: 10px 30px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;
}

.topcontent__downloadbutton:hover{
    color: #f64c08;
    background-color: white;
    font-weight: 600;
}

.topcontent__workbutton{
    color: white;
    background-color: transparent;
    font-size: 14px;
    padding: 9px 45px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;
    border: 1px solid white !important;
}

.topcontent__workbutton:hover{
    color: black;
    background-color: white;
    font-weight: 600;
}

@media screen and (max-width: 800px){
    
    .topcontent__container{
        position: absolute;
        top: 150px;
        left: 1em;
        width: 100vw;
    }

    .topcontent__container > h1{
        font-size: 20px;
        font-family: 'Montserrat', sans-serif;
    }

    .topcontent__container > p {
        letter-spacing: 1px;
        text-align: left;
        padding-left: 3px;
        padding-top: 10px;
    }

    .topcontent__container > a > button{
        margin-top: 25px;
    }

}