@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

.skillContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 200px 0;
}

.skillContainer__text{
    color: white;
    flex: 0.6;
    text-align: left;
    max-width: 500px;
    margin-bottom: 20px;
}

.skillContainer__text > h2{
    font-size: 30px;
}

.skillContainer__image{
    flex: 0.4;
    text-align: center;
    margin-right: 100px;
}

.skillContainer__image > img{
    width: 600px;
    border-radius: 3px;
    object-fit: contain;
}

.skillContainer__skillset > h5{
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    padding-bottom: 30px;
    padding-top: 10px;
}

.skillContainer__slider > .css-eglki6-MuiLinearProgress-root{
background-color: gray;
}



.skillContainer__slider > .css-eglki6-MuiLinearProgress-root > .css-5xe99f-MuiLinearProgress-bar1{
    background-color: #f64c08;
}

@media screen and (max-width: 800px){
.skillContainer {
    display: flex;
    margin: 130px 0;
    flex-direction: column;
}

.skillContainer__image{
    margin: 0;
}

.skillContainer__image > img {
    width: 300px;
    border-radius: 3px;
    object-fit: contain;
}

.skillContainer__text{
    width: 100vw;
    margin-top: 10px;
    margin-left: 20px;
}

}